import React, { useState } from 'react';
import { Container, Carousel, Navbar, Nav, Row, Col, Figure, Spinner } from 'react-bootstrap';
import '../style/Home.scss'
import '../style/CarousselItem.scss';
import MyCard from './MyCard';
import { euro, getFormations, racineUrl } from '../database/DataApi';
import AOS from 'aos';
import { useEffect } from 'react';
import FormContact from './FormContact';
import Footer from './Footer';


const Home = () => {
    const [formations, setFormations] = useState([]);
    const [loading, setLoading] = useState(false);


    useEffect(() => {
        AOS.refreshHard();
       
        chargerLesFormations();

    }, []);

    const chargerLesFormations = () => {
        setLoading(true);

        getFormations().then((data) => {
            setFormations(data);
            setLoading(false);
        }).catch((err) => console.log("error db :", err));
    }


    return (
        <>
            <Container fluid>
                <Navbar collapseOnSelect expand="md" bg="dark" variant="dark">
                    <Container fluid>
                        <Navbar.Brand href="#home">
                            <img
                                alt="logo"
                                src={racineUrl + "/logoD.jpg"}
                                width="30"
                                height="30"
                                className="d-inline-block align-top"
                            />{' '}
                            Dag Develop
                        </Navbar.Brand>
                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />

                        <Navbar.Collapse id="responsive-navbar-nav">
                            <Nav className="me-auto"></Nav>
                            <Nav navbarScroll >
                                <Nav.Link href="#formations" className="nav-item">Formations</Nav.Link>
                                <Nav.Link href="#about" className="nav-item">Qui suis-je ?</Nav.Link>
                                <Nav.Link href="#postuler" className="nav-item">Postuler</Nav.Link>
                            </Nav>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
                <Carousel className="carousel" fade>
                    <Carousel.Item>
                        <img
                            className="d-block w-100 carousselImage"
                            src={racineUrl + "/prog-1.jpg"}
                            alt="First slide"
                        />
                        <Carousel.Caption>
                            <h1>Formation en programmation</h1>
                            <p>Fait tes premiers pas dans le monde de la programmation informatique </p>
                            <div className="mb-4 mt-4">
                                <a className='button' href="#formations" >Nos Formations</a>
                            </div>

                        </Carousel.Caption>

                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100 h-50 carousselImage"
                            src={racineUrl + "/carousel.jpg"}
                            alt="Second slide"
                        />

                        <Carousel.Caption>
                            <h1>Nos Formateurs</h1>
                            <p>Des passionnes de la programmation informatique, qui partagent tout leur savoir avec vous !</p>
                            <div className="mb-4 mt-4">
                                <a className='button' href="#about" >Collaborateurs</a>
                            </div>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100 carousselImage"
                            src={racineUrl + "/prog-1.jpg"}
                            alt="Third slide"
                        />

                        <Carousel.Caption>
                            <h1>Devenir Formateur</h1>
                            <p>
                                Chez nous vous avez la possibilit de donner des formations dans le domaine de votre choix,
                                et ceci contre petite rmunration !
                            </p>
                            <div className="mb-4 mt-4">
                                <a className='button' href="#postuler" >Postuler</a>
                            </div>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            </Container>

            <Container id="formations">
                <Container data-aos="fade-up" className="mt-5 mb-5 titre">
                    <h1 >Nos Formations</h1>
                </Container>
                <div data-aos="fade-up">
                    {formations.length !== 0 ? (
                        <Row lg={4} md={2} sm={1}>
                            {
                                !loading && formations.map((f, key) => {
                                    return (
                                        <Col key={key} className="mb-4">
                                            <MyCard image={f.image} title={f.titre}
                                                text={f.description}
                                                price={`${f.prix} ${euro}`}
                                                currentFormation={f}
                                            />
                                        </Col>
                                    );
                                })
                            }
                            {
                                loading && (
                                    <Spinner animation="border" role="status">
                                        <span className="visually-hidden">Loading...</span>
                                    </Spinner>
                                )
                            }
                        </Row>
                    ) : (
                        <>
                            <h3>Impossible de charger les formations !</h3>
                            <h5 style={{ color: 'white',  backgroundColor: 'darkCyan', padding: '6px', fontStyle: 'bold'}}>Recharger la page ou vrifier votre connexion internet !</h5>
                        </>
                    )}
                </div>
            </Container>


            <Container id="about">
                <Container className="mt-5 mb-5 titre" data-aos="slide-left">
                    <h1 >Qui suis-je ?</h1>
                </Container>
                <Container className='background'>
                    <div data-aos="slide-up" data-aos-duration="2000">
                        <div>
                            <Row className='row'>
                                <Col sm={7} md={3}>
                                    <div data-aos="zoom-out-up" data-aos-duration="1000">
                                        <Figure>
                                            <Figure.Image
                                                width="350rem"
                                                alt="350X350"
                                                src={`${racineUrl}/Vially.jpg`}
                                            />
                                            <Figure.Caption>
                                                <p>Crdit photo : <a rel="noreferrer" target={'_blank'} href="https://www.instagram.com/kwizerafilms">Didier Kwizera</a></p>
                                            </Figure.Caption>
                                        </Figure>

                                    </div>
                                </Col>
                                <Col sm={6} className="ml-0">
                                    <Row sm={1} className="mb-3" data-aos="slide-right" >
                                        <em><p>" <strong>Vially Dag</strong>, originaire du <a href="https://fr.wikipedia.org/wiki/Cameroun">cameroun</a>, rsidant en belgique et prsentement dans la ville de namur, est un jeune tudiant en
                                            informatique de gestion dans la <a rel="noreferrer" target={'_blank'} href="https://www.henallux.be/namur-iesn">haute cole Henallux (IESN)</a>.
                                        </p>
                                            <p>Sa principale passion est la programmation informatique, domaine dans lequel il souhaiterait exceller afin de devenir l'un des meilleurs ".</p></em>
                                    </Row>
                                    <Row data-aos="fade-zoom-out" data-aos-duration="1000">
                                        <p>Ayant dja ralis plusieurs projets (personnels et scolaires), et aid plusieurs personnes en programmation,
                                            j'ai dcid de mettre encore plus  profit mes comptences, afin que plusieurs en bnficient.
                                            C'est facile aujourd'hui d'apprendre un langage de programation en autodidacte, mais le plus souvent face  certains problmes,
                                            on a pas les rponses adquates car, personnes vers qui se tourner.
                                        </p>
                                        <p>En plus de recevoir une formation en utilisant les meilleures mthodologies qui existent, vous aurez un accompagnement 7jrs/7 jusqu' ce que votre formation
                                            prenne fin. Des exercices bien labors et accompagns, et  la fin un projet que vous devrez raliser tout seul. Personne ne partira comme il est venu.
                                        </p>
                                    </Row>
                                </Col>
                            </Row>
                        </div>
                    </div>
                </Container>
            </Container>

            <Container fluid id="postuler" className="mb-6 mt-6">
                <Container className="mt-5 mb-5 titre" data-aos="fade-up">
                    <h1 >Nous rejoindre ?</h1>
                </Container>
                <div className='form mb-6 mt-6' data-aos="fade-up">
                    <p>Nous sommes toujours  la recherche de formateurs pour agrandir notre quipe. Tu es tudiant ou professionnel, tu possdes de
                        trs bonnes connaissances dans un certain langage (C++, C#, python, MySQL, etc...), librairie ou framework (React js, Node js, spring-boot, etc...), tu es capable de transmttre ton savoir en toute facilit, alors n'hsite pas  prendre contact avec nous via le formulaire i-dessous et rejoins la grande famille <em><strong>Dag Develop.
                        </strong></em> </p><p>Nous tudions tous les dossiers et y donnons suite dans les 3 jours ouvrables ! Nous avons hte de vous rencontrer .</p>
                </div>
                <FormContact />
            </Container>

            <Footer />
        </>
    );
};

export default Home;